let _this;
import config from '@/api/config.js';
import {request} from '@/api/reques.js'
import {goWindow} from '@/api/navigate.js' // 页面跳转
export default {
	props:{
		carId:{
			type:Number
		},
		brandObj:{
			type:Object
		},
	},
	data() {
		return {
			goodsList: [],
			page_count:0,
			brandId:0,
			systemId:0,
		}
	},
	watch:{
		carId(id){
			_this.getGoodsList();
		},
		brandObj:{
			handler(obj) {
				_this.brandId = obj.brandId;
				_this.systemId = obj.categoryId;
				_this.getGoodsList();
			},
			deep: true
		}
		
	},
	created() {
		this.getGoodsList();
		_this = this;
	},
	filters:{
		filterImg(src){
			if(src){
				return config.domain + src
			}
		}
	},
	methods: {
		handleDetails(goodsId,stockId) {
			//console.log(goodsId,stockId)
			let kcId = {
				goodsId:goodsId,
				stockId:stockId
			}
			goWindow('/pages/kucunDetails/kucunDetails?id=',JSON.stringify(kcId))
		},
		// 获取商品列表
		getGoodsList(page_index = 1) {
			let _this = this;
			uni.showLoading({
				title: '加载中'
			});
			request("/index/getStockGoodsList", {
					page_index: page_index,
					page_size: 10,
					car_type: _this.carId,
					brands_id: _this.brandId,
					system_id: _this.systemId,
				}, "GET")
				.then((res) => {
					let data = res.data.data.data
					if(page_index > 1){
						data = this.goodsList.concat(data)
					}
					this.goodsList = data;
					this.page_count = res.data.data.page_count;
					this.$emit("changePage",page_index,this.page_count)
					setTimeout(() => {
						uni.hideLoading();
					}, 500);
				})
		},
		handleErr(e, index) {
			if (e.type == 'error') {
				this.goodsList[index].pic_cover = "../../../static/images/list.jpg"
			}
		},
		transferTime(time){
			var date = new Date(parseInt(time) * 1000);
			let m = date.getMonth()+1;
			m = m < 10 ? ('0' + m) : m;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let newDate = m+ "-" +d;
			return newDate
		}
		
		
	}
}
